* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #2f303a;
  padding: 20px;
}

h1 {
  margin: 0;
}

main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.search-form {
  margin-bottom: 20px;
  display: flex;
}

.search-form input[type="text"] {
  flex: 1;
  padding: 10px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #2196f3;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 10px;
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.photo-card .info {
  padding: 10px;
}

.photo-card .info-item {
  margin: 0;
  padding: 5px 0;
}

.load-more {
  color: #333;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  display: none;
}

